@import "https://fonts.googleapis.com/css2?family=Montserrat&display=swap";
@import "https://fonts.googleapis.com/css2?family=Fredoka+One&family=Montserrat&display=swap";
@import "https://fonts.googleapis.com/css2?family=Russo+One&display=swap";
@import "https://fonts.googleapis.com/css2?family=Lobster&display=swap";
@import "https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap";
html {
  min-height: 100%;
  position: relative;
}

a {
  color: #fff;
  -webkit-text-decoration-line: none;
  text-decoration-line: none;
}

a:hover {
  color: #888;
}

.opcioActiva {
  color: #0ff;
}

h1 {
  font-family: Russo One, sans-serif;
}

.general {
  background-color: #000;
  background-image: url("fonsWeb.da474866.jpg");
  flex-direction: column;
  margin: 0;
  font-family: Montserrat, sans-serif;
  display: flex;
}

#cos {
  background-position: center;
  background-repeat: no-repeat;
}

header {
  z-index: 10;
  height: auto;
  min-width: 100%;
  text-align: center;
  background-color: #333;
  background-image: url("fonsCap.0e688d77.jpg");
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: fixed;
  box-shadow: 0 8px 8px #00000026;
}

#titolCap {
  color: #fff;
  text-transform: uppercase;
  padding-left: 20px;
}

#barraMenu {
  color: #fff;
  justify-content: center;
  font-weight: bold;
  display: flex;
}

#menu {
  flex-direction: row;
  padding: 10px;
  list-style: none;
  display: flex;
}

.opcio {
  padding-left: 10px;
  padding-right: 10px;
}

main {
  margin-top: 80px;
  margin-bottom: 100px;
}

#containerCat {
  width: 100%;
  opacity: .9;
  background: linear-gradient(#000, #a22);
  flex-flow: wrap;
  justify-content: center;
  margin: auto auto 0;
  display: flex;
}

.imgCat {
  max-height: 65vh;
  max-width: 100%;
  object-fit: cover;
}

.titolCat {
  color: #fff;
}

.llista {
  flex-flow: wrap;
  align-content: center;
  display: flex;
}

.puntCat {
  width: 160px;
  text-align: center;
  background-color: #555;
  border-radius: 5px;
  margin: 10px;
  padding: 5px;
  list-style-type: none;
  display: block;
}

.puntActiu {
  width: 160px;
  text-align: center;
  background-color: #faebd7;
  border-radius: 5px;
  margin: 10px;
  padding: 5px;
  list-style-type: none;
  display: block;
}

.puntTitol {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  font-size: .8em;
  font-weight: 800;
}

.miniatura {
  height: 100px;
  width: 150px;
  object-fit: cover;
  border-radius: 5px;
  box-shadow: 0 8px 8px #00000026;
}

.miniatura:hover {
  cursor: pointer;
}

.containerSongs {
  flex-flow: wrap;
  justify-content: center;
  padding: 10px;
  display: flex;
}

.song {
  width: 350px;
  background-color: #555;
  border-radius: 30px;
  flex-direction: column;
  margin: 10px;
  padding: 5px;
  display: flex;
}

.song .videoSong {
  max-width: 350px;
  align-items: center;
  margin: 10px auto;
}

.dadesSong {
  vertical-align: text-top;
  max-width: 350px;
  height: 100%;
  padding: 10px;
}

.dadesSong ul li {
  color: bisque;
  margin-top: 5px;
  font-size: .9rem;
  list-style-type: none;
}

.dadesSong ul li p {
  margin: 5px 0 0;
}

.dadesSong ul li a {
  color: bisque;
}

.titolSong {
  color: #fff;
  text-align: left;
  font-size: 1rem;
}

footer {
  color: #fff;
  min-width: 100%;
  background-color: #333;
  background-image: url("fonsCap.0e688d77.jpg");
  flex-direction: row;
  justify-content: space-around;
  display: flex;
  position: absolute;
  bottom: 0;
  box-shadow: 0 -8px 8px #00000026;
}

footer div {
  text-align: center;
  flex: auto;
  padding-right: 20px;
}

@media screen and (max-width: 700px) {
  header {
    height: 150px;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
  }

  #titolCap {
    color: #fff;
    text-transform: uppercase;
    padding: 0;
  }

  #menu {
    flex-direction: row;
    margin: 0;
    padding: 0;
  }

  #containerCat {
    width: 100%;
    flex-direction: row;
    justify-content: center;
    margin: 0;
  }

  .puntCat {
    width: 42%;
    margin-bottom: 10px;
    margin-left: 0;
    margin-right: 5px;
  }

  .puntActiu {
    width: 85%;
    margin-bottom: 10px;
    margin-left: 5px;
    margin-right: 5px;
  }

  .miniatura {
    width: 90%;
    height: 100px;
    object-fit: cover;
  }

  main {
    margin-top: 150px;
    margin-bottom: 250px;
  }

  footer {
    flex-direction: column;
  }
}

